//== Default (dft) Font Size Mixins (with cooresponding line-heights)
//
//## These are the only font-sizes that will be used project-wide
.dft-font-1() {
  font-size: @dft-font-1;
  line-height: 17px;
}

.dft-font-2() {
  font-size: @dft-font-2;
